.initial-form {
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.9rem;
  flex-wrap: wrap;
  align-content: center;

  .submit-button {
    border: none;
    border-radius: 0.5rem;
    background-color: $main-color;
    color: $main-text-color;
    padding: 0.2rem 0.4rem;
    transition: 0.5s;
    &:hover {
      background-color: $main-text-color;
      color: $main-color;
    }

    &:active {
      background-color: $main-color;
      color: $main-text-color;
    }
  }

  #edit-button {
    border: none;
    background-color: rgb(196, 176, 0);
    color: $main-text-color;
    padding: 0.2rem 0.5rem;
    border-radius: 0.5rem;
    transition: 0.5s;
    &:hover {
      background-color: $main-text-color;
      color: rgb(196, 176, 0);
    }
  }

  #close-button {
    border: none;
    background-color: rgba(255, 0, 0, 0.514);
    color: $main-text-color;
    padding: 0.2rem 0.5rem;
    border-radius: 0.5rem;
    transition: 0.5s;
    &:hover {
      background-color: red;
    }
  }

  #close-button {
    border: none;
    background-color: rgba(255, 0, 0, 0.514);
    color: $main-text-color;
    padding: 0.2rem 0.5rem;
    border-radius: 0.5rem;
    transition: 0.5s;
    &:hover {
      background-color: red;
    }
  }
}

.todos-container {
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 1rem;
  .completed {
    background-color: $completed-color;
    color: $main-text-color;
  }
  .todos {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40rem;
    gap: 0.5rem;
    flex: 0 0 5rem;
    border: 0.2rem solid $main-color;
    border-radius: 0.7rem;
    button {
      border: none;
      width: 2rem;
      border-radius: 0.3rem;
    }
    #complete-button {
      background-color: $completed-color;
      color: $main-text-color;
    }
    #edit-button {
      background-color: rgb(196, 176, 0);
      color: $main-text-color;
    }

    #delete-button {
      background-color: rgb(196, 46, 0);
      color: $main-text-color;
    }
  }
}

@media only screen and (max-width: 480px) {
  .todos-container {
    .todos {
      width: 90%;
    }
  }
}
