* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
#root,
.App {
  height: 100vh;
}

html {
  font-size: 60.25%;
  font-family: "Nunito", sans-serif;
  font-family: "Roboto", sans-serif;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 6%;
  background-color: #134ae2;
}

header h1 {
  flex: 1 0 1rem;
  text-align: left;
  margin-left: 5rem;
  color: #ffffff;
}

header .lines-container {
  display: none;
  margin-right: 5%;
}

header .lines-container div {
  width: 3rem;
  height: 0.4rem;
  background-color: #ffffff;
  margin: 5px 0;
  transition: 0.6s;
}

header .lines-container #line-1-rotate {
  transform: rotate(45deg) translate(0.1rem, 0.76rem);
}

header .lines-container #line-2-dis {
  display: none;
}

header .lines-container #line-3-rotate {
  transform: rotate(-45deg) translate(-0.2rem, -0.6rem);
}

header nav {
  flex: 1 0 5rem;
}

header nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7rem;
  list-style: none;
}

header nav ul li a {
  font-size: 1.5rem;
  text-decoration: none;
  color: #ffffff;
  padding: 0.3rem;
  border-radius: 0.5rem;
  transition: 0.5s;
}

header nav ul li a:hover {
  background-color: #ffffff;
  color: #134ae2;
}

header .mobile-menu {
  display: none;
}

header .display-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  top: 3.5rem;
  height: 5rem;
  width: 100%;
  background-color: #134ae2;
}

@media only screen and (max-width: 480px) {
  header nav {
    display: none;
  }
  header .lines-container {
    display: block;
  }
}

.counter-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  margin-top: 1rem;
}

.homeContainer {
  height: 94%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-content: center;
}

.homeContainer .banner-container {
  width: 50%;
  animation-name: from-left-to-right;
  animation-duration: 5s;
}

.homeContainer .banner-container h1 {
  font-size: 6rem;
}

.homeContainer .banner-container a {
  text-decoration: none;
  font-size: 2rem;
  background-color: #134ae2;
  color: #ffffff;
  padding: 0.2rem 0.4rem;
  border-radius: 0.5rem;
  transition: 1s;
}

.homeContainer .banner-container a:hover {
  background-color: #ffffff;
  color: #134ae2;
}

@keyframes from-left-to-right {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  50% {
    opacity: 0.5;
    transform: translateX(20%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@media only screen and (max-width: 480px) {
  .homeContainer {
    text-align: center;
  }
  .homeContainer .banner-container {
    text-align: center;
    width: 100%;
  }
}

.initial-form {
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.9rem;
  flex-wrap: wrap;
  align-content: center;
}

.initial-form .submit-button {
  border: none;
  border-radius: 0.5rem;
  background-color: #134ae2;
  color: #ffffff;
  padding: 0.2rem 0.4rem;
  transition: 0.5s;
}

.initial-form .submit-button:hover {
  background-color: #ffffff;
  color: #134ae2;
}

.initial-form .submit-button:active {
  background-color: #134ae2;
  color: #ffffff;
}

.initial-form #edit-button {
  border: none;
  background-color: #c4b000;
  color: #ffffff;
  padding: 0.2rem 0.5rem;
  border-radius: 0.5rem;
  transition: 0.5s;
}

.initial-form #edit-button:hover {
  background-color: #ffffff;
  color: #c4b000;
}

.initial-form #close-button {
  border: none;
  background-color: rgba(255, 0, 0, 0.514);
  color: #ffffff;
  padding: 0.2rem 0.5rem;
  border-radius: 0.5rem;
  transition: 0.5s;
}

.initial-form #close-button:hover {
  background-color: red;
}

.initial-form #close-button {
  border: none;
  background-color: rgba(255, 0, 0, 0.514);
  color: #ffffff;
  padding: 0.2rem 0.5rem;
  border-radius: 0.5rem;
  transition: 0.5s;
}

.initial-form #close-button:hover {
  background-color: red;
}

.todos-container {
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 1rem;
}

.todos-container .completed {
  background-color: rgba(16, 212, 98, 0.808);
  color: #ffffff;
}

.todos-container .todos {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40rem;
  gap: 0.5rem;
  flex: 0 0 5rem;
  border: 0.2rem solid #134ae2;
  border-radius: 0.7rem;
}

.todos-container .todos button {
  border: none;
  width: 2rem;
  border-radius: 0.3rem;
}

.todos-container .todos #complete-button {
  background-color: rgba(16, 212, 98, 0.808);
  color: #ffffff;
}

.todos-container .todos #edit-button {
  background-color: #c4b000;
  color: #ffffff;
}

.todos-container .todos #delete-button {
  background-color: #c42e00;
  color: #ffffff;
}

@media only screen and (max-width: 480px) {
  .todos-container .todos {
    width: 90%;
  }
}
