header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 6%;
  background-color: $main-color;
  h1 {
    flex: 1 0 1rem;
    text-align: left;
    margin-left: 5rem;
    color: $main-text-color;
  }

  .lines-container {
    display: none;
    margin-right: 5%;
    div {
      width: 3rem;
      height: 0.4rem;
      background-color: $main-text-color;
      margin: 5px 0;
      transition: 0.6s;
    }
    #line-1-rotate {
      transform: rotate(45deg) translate(0.1rem, 0.76rem);
    }

    #line-2-dis {
      display: none;
    }

    #line-3-rotate {
      transform: rotate(-45deg) translate(-0.2rem, -0.6rem);
    }
  }

  nav {
    flex: 1 0 5rem;
    ul {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 7rem;
      list-style: none;
      li {
        a {
          font-size: 1.5rem;
          text-decoration: none;
          color: $main-text-color;
          padding: 0.3rem;
          border-radius: 0.5rem;
          transition: 0.5s;
          &:hover {
            background-color: $main-text-color;
            color: $main-color;
          }
        }
      }
    }
  }

  .mobile-menu {
    display: none;
  }

  .display-menu {
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    top: 3.5rem;
    height: 5rem;
    width: 100%;
    background-color: $main-color;
  }
}

@media only screen and (max-width: 480px) {
  header {
    nav {
      display: none;
    }

    .lines-container {
      display: block;
    }
  }
}
